body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.container {
  margin-top: 10px;
  background: #C9D6FF;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #E2E2E2, #C9D6FF);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#E2E2E2), to(#C9D6FF));
  background: -webkit-linear-gradient(right, #E2E2E2, #C9D6FF);
  background: -o-linear-gradient(right, #E2E2E2, #C9D6FF);
  background: linear-gradient(to left, #E2E2E2, #C9D6FF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}



